@font-face {
  font-family: "Montserrat";
  src: url("./fonts/Montserrat-VariableFont_wght.ttf") format("truetype");
}
@font-face {
  font-family: "toruk";
  src: url("./fonts/torukscregular-z8ma1.ttf") format("truetype");
}
body {
  cursor: url(https://i.postimg.cc/sxVJvS2H/cursor01.png) 0 5, auto;
  font-size: 16px;
  font-weight: 400;
  font-family: "Montserrat", "toruk", "sans-serif";
  line-height: 1.5;
}

.App {
  overflow: hidden;
  background: #081019;
}

header {
  position: fixed;
  z-index: 999;
  top: 0;
  width: 100%;
  height: 68px;
  background: linear-gradient(to top, rgba(36, 58, 82, 0), #000);
  text-align: center;
}
header h1.logo {
  position: relative;
  z-index: 1000;
  text-align: center;
  font-size: 1rem;
  color: #fff;
}
header h1.logo img {
  filter: brightness(0) invert(1);
  height: 68px;
  transition: 0.25s linear;
}
header h1.logo img:hover {
  filter: brightness(75%);
  transform: scale(1.05);
}
header h1.logo img:active {
  filter: brightness(50%);
  transform: scale(0.95);
}
header nav {
  position: fixed;
  z-index: 999;
  top: 0;
}
header nav ul {
  margin-top: 80px;
  margin-left: -22px;
  display: flex;
  justify-content: center;
  width: 100vw;
  gap: 128px;
}
header nav ul li {
  width: 1px;
  font-size: 1.25rem;
  font-weight: 400;
  font-family: "toruk";
  letter-spacing: 1.25px;
  color: #243B53;
  text-shadow: -1px 0 rgba(255, 255, 255, 0.5), 0 1px rgba(255, 255, 255, 0.5), 1px 0 rgba(255, 255, 255, 0.5), 0 -1px rgba(255, 255, 255, 0.5);
}

main.visual {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100vw 100vh;
  background-color: #000;
  transition: background-image 1s ease-in-out;
}
main.visual .container {
  width: 100%;
  height: calc(100% - 128px);
}
main.visual .container .content-box {
  display: flex;
  align-items: flex-end;
  height: 100%;
  margin-left: 48px;
}
main.visual .container .content-box:after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0;
  height: 0;
  border: 1px solid rgba(255, 255, 255, 0.75);
  border-radius: 50%;
  animation: ripple 2s ease-in-out 1s infinite;
}
main.visual .container .content-box .inner {
  position: relative;
  width: 25%;
  height: 65%;
  border-radius: 8px;
  box-shadow: -4px 8px 24px 0 rgba(0, 0, 0, 0.5);
}
main.visual .container .content-box .inner:after {
  opacity: 0;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid #f2f2f2;
}
main.visual .container .content-box .inner:before {
  opacity: 0;
  content: "";
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-20%, -50%);
  width: 0;
  height: 0;
  border-bottom: 14px solid transparent;
  border-top: 14px solid transparent;
  border-left: 25px solid #f2f2f2;
  border-right: 25px solid transparent;
}
main.visual .container .content-box .inner img {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  filter: saturate(75%);
  transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.84);
}
main.visual .container .content-box .inner img:hover {
  transform: scale(0.975);
  filter: brightness(50%) invert(0);
}
main.visual .container .content-box .inner img:active {
  transform: scale(0.925);
  filter: brightness(55%) invert(0);
}
main.visual .container .content-box .inner:hover::before, main.visual .container .content-box .inner:hover::after {
  opacity: 1;
  transition: 1s cubic-bezier(0.25, 0.46, 0.45, 0.84);
}
main.visual .container .content-box h2 {
  margin-left: 28px;
  font-size: 5vw;
  font-weight: 400;
  font-family: "toruk", "sans-serif";
  color: #f2f2f2;
  letter-spacing: 1.5px;
  line-height: 1;
}

.video-modal {
  display: none;
  position: fixed;
  z-index: 99999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  transition: opacity 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.84);
  opacity: 0;
}

.video-modal-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.video-modal-content .video-box {
  position: relative;
  width: 70vw;
  height: 66vh;
}
.video-modal-content .video-box video {
  width: 100%;
  height: 100%;
  background: #000;
}
.video-modal-content .video-box .close-button {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0.2em 0.8em;
  font-size: 1rem;
  transition: 0.15s cubic-bezier(0.25, 0.46, 0.45, 0.84);
  cursor: inherit;
  border: none;
  outline: none;
  background: rgba(255, 255, 255, 0.1);
  color: #f2f2f2;
}
.video-modal-content .video-box .close-button:hover {
  background: rgba(255, 255, 255, 0.3);
}

section.page1 {
  overflow: hidden;
  user-select: none;
  width: 100vw;
  height: 100vh;
  background: #0e081a;
  background-size: 20px 20px;
  background-image: repeating-radial-gradient(rgba(105, 155, 255, 0.1) 0px, #0e081a 2px, black 48px);
}
section.page1 .satellites {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  transform-style: preserve-3d;
  animation: rotate-axis 25s linear infinite;
}
section.page1 .satellite {
  position: absolute;
  transform: rotateY(calc(var(--i) * 1deg)) translateZ(500px);
}
section.page1 .satellite img {
  user-select: none;
  -webkit-user-drag: none;
  -webkit-touch-callout: none;
  position: relative;
  box-shadow: 0 0 20px #000;
  display: block;
  border-radius: 4px;
  width: 150px;
  height: 150px;
}
section.page1 .satellite p {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 144px;
  padding: 0.5em 0;
  padding-left: 0.5em;
  font-size: 0.75rem;
  font-family: "toruk";
  line-height: 1;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
}

@keyframes rotate-axis {
  from {
    transform: rotateZ(350deg) perspective(800px) rotateY(0deg);
  }
  to {
    transform: rotateZ(350deg) perspective(800px) rotateY(360deg);
  }
}
