@keyframes tail {
  0% {
    height: 0;
    opacity: 0;
  }
  25% {
    opacity: 100%;
  }
  75% {
    opacity: 100%;
  }
  100% {
    height: 50%;
    opacity: 0;
  }
}
@keyframes shooting {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(100%);
  }
}
.animating {
  animation-name: slide-down;
  animation-duration: 1.5s;
  animation-timing-function: ease-in-out;
}

@keyframes slide-down {
  0% {
    transform: translateY(-75%);
    opacity: 0;
  }
  50% {
    transform: translateY(0%);
  }
  75% {
    transform: translateY(1%);
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.animating.reverse {
  animation-name: slide-up;
  animation-duration: 1.5s;
  animation-timing-function: ease-in-out;
}

@keyframes slide-up {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  25% {
    transform: translateY(1%);
  }
  50% {
    transform: translateY(0%);
  }
  100% {
    transform: translateY(-75%);
    opacity: 0;
  }
}
@keyframes ripple {
  50% {
    border: 3px solid rgba(255, 255, 255, 0.5);
  }
  100% {
    border: 0.1px solid rgba(255, 255, 255, 0.25);
    width: 256px;
    height: 256px;
    opacity: 0;
  }
}
@media screen and (max-width: 1028px) {
  body {
    font-size: 10px;
  }
  main.visual {
    overflow: hidden;
  }
  main.visual .container {
    height: 100vh;
    margin-top: 180px;
  }
  main.visual .container .content-box {
    flex-direction: column;
    align-items: center;
  }
  main.visual .container .content-box::after {
    display: none;
  }
  main.visual .container .content-box h2 {
    margin-top: 1em;
    text-align: center;
  }
  main.visual .container .content-box .inner {
    width: 80%;
    height: 50%;
  }
}
body {
  overflow: hidden;
  font-family: "Roboto", serif;
  background: linear-gradient(135deg, black, #220033);
}

.carousel {
  position: relative;
  z-index: 1;
  height: 100vh;
  overflow: hidden;
  pointer-events: none;
}

.carousel-item {
  --items: 10;
  --width: clamp(150px, 30vw, 300px);
  --height: clamp(200px, 40vw, 400px);
  --x: calc(var(--active) * 800%);
  --y: calc(var(--active) * 200%);
  --rot: calc(var(--active) * 120deg);
  --opacity: calc(var(--zIndex) / var(--items) * 3 - 2.1);
  overflow: hidden;
  position: absolute;
  z-index: var(--zIndex);
  top: 50%;
  left: 50%;
  width: var(--width);
  height: var(--height);
  margin: calc(var(--height) * -0.5) 0 0 calc(var(--width) * -0.5);
  background: #000;
  border-radius: 10px;
  box-shadow: 0 10px 50px 10px #000;
  transform-origin: 0% 100%;
  transform: translate(var(--x), var(--y)) rotate(var(--rot));
  transition: transform 0.8s cubic-bezier(0, 0.02, 0, 1);
  pointer-events: all;
  user-select: none;
}
.carousel-item .carousel-box {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 0.8s cubic-bezier(0, 0.02, 0, 1);
  opacity: var(--opacity);
  font-family: "toruk";
}
.carousel-item .carousel-box:before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.5));
}
.carousel-item .title {
  position: absolute;
  z-index: 1;
  color: #fff;
  bottom: 20px;
  left: 20px;
  transition: opacity 0.8s cubic-bezier(0, 0.02, 0, 1);
  font-size: clamp(20px, 3vw, 30px);
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
}
.carousel-item .num {
  position: absolute;
  z-index: 1;
  color: #fff;
  top: 10px;
  left: 20px;
  transition: opacity 0.8s cubic-bezier(0, 0.02, 0, 1);
  font-size: clamp(20px, 10vw, 80px);
}
.carousel-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: none;
}

.layout {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-size: 20px 20px;
  background-image: repeating-radial-gradient(rgba(105, 155, 255, 0.1) 0px, #0e081a 2px, black 48px);
}
.layout:before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 90px;
  width: 10px;
  height: 100%;
  border: 1px solid #fff;
  border-top: none;
  border-bottom: none;
  opacity: 0.15;
}
.layout .box {
  position: absolute;
  bottom: 0;
  left: 30px;
  color: #fff;
  transform-origin: 0% 10%;
  transform: rotate(-90deg);
  font-size: 9px;
  line-height: 1.4;
  text-transform: uppercase;
  opacity: 0.4;
}

p.wave-text {
  position: absolute;
  top: 120%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: "Montserrat";
  font-weight: 900;
  font-size: 1.25rem;
  text-transform: uppercase;
  width: 100%;
  height: 100%;
  color: #fff;
  perspective: 900px;
}
p.wave-text .char {
  offset-path: path("M.5 122.7s24.7-275 276.9 0c327.1 356.7 266.1-330.3 548-33.3 256.9 270.7 271.1 0 271.1 0");
  offset-distance: calc(var(--char-index) * 1.75rem);
  position: absolute;
  animation: wave 2200ms cubic-bezier(0.62, 0.01, 0.42, 1.01) infinite alternate calc(var(--char-index) * 20ms), wave2 2250ms cubic-bezier(0.62, 0.01, 0.42, 1.01) infinite alternate calc(var(--char-index) * 20ms);
  transform: perspective(800px) scale(1.5) rotate3d(0, 0, 0, 0);
}

@keyframes wave {
  100% {
    offset-distance: calc(var(--char-index) * 1.75rem + 100px);
    transform: perspective(800px) scale(1) rotate3d(1, 0, 0, 180deg);
    color: #1ca8c0;
  }
}
@keyframes wave2 {
  100% {
    offset-distance: calc(var(--char-index) * 2.75rem + 300px);
    transform: perspective(800px) scale(1.75) rotate3d(1, 0, 0, 360deg);
    color: transparent;
  }
}
p.wave-text1 {
  position: absolute;
  top: 18%;
  left: 55%;
  font-family: "Montserrat";
  font-weight: 900;
  font-size: 1.25rem;
  text-transform: uppercase;
  width: 100%;
  height: 100%;
  color: transparent;
  perspective: 900px;
}
p.wave-text1 .char {
  offset-path: path("M.5 122.7s24.7-275 276.9 0c327.1 356.7 266.1-330.3 548-33.3 256.9 270.7 271.1 0 271.1 0");
  offset-distance: calc(var(--char-index) * 1.75rem);
  position: absolute;
  animation: wave1 2200ms cubic-bezier(0.62, 0.01, 0.42, 1.01) infinite alternate-reverse calc(var(--char-index) * 20ms), wave3 2200ms cubic-bezier(0.62, 0.01, 0.42, 1.01) infinite alternate-reverse calc(var(--char-index) * 20ms);
  transform: perspective(800px) scale(1) rotate3d(0, 0, 0, 0);
}

@keyframes wave1 {
  100% {
    offset-distance: calc(var(--char-index) * 2rem + 100px);
    transform: perspective(800px) scale(1.5) rotate3d(1, 0, 0, 180deg);
    color: #1ca8c0;
  }
}
@keyframes wave3 {
  100% {
    offset-distance: calc(var(--char-index) * 2.45rem + 300px);
    transform: perspective(800px) scale(1.75) rotate3d(1, 0, 0, 360deg);
    color: #fff;
  }
}
section.page3 {
  overflow-x: hidden;
  user-select: none;
  width: 100vw;
  height: 100vh;
  background: #0e081a;
  background-size: 20px 20px;
  background-image: repeating-radial-gradient(rgba(105, 155, 255, 0.1) 0px, #0e081a 2px, black 48px);
}

article.image-slider {
  position: relative;
  top: 52%;
  left: 50%;
  width: 81.25vw;
  height: 600px;
  border-radius: 5px;
  box-shadow: 0px 50px 100px rgba(0, 0, 0, 0.4);
  transform: translate(-50%, -48%);
}
article.image-slider .images {
  overflow: hidden;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
article.image-slider .images img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transform: scale(1.1);
  transition: all 500ms ease-in-out;
}
article.image-slider .images img.active {
  opacity: 1;
  transform: scale(1);
}
article.image-slider .progress-bar {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: #ffe24f;
}
article.image-slider .back-btn,
article.image-slider .next-btn {
  position: absolute;
  top: 0px;
  width: 40px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #fff;
  font-size: 25px;
  background: rgba(0, 0, 0, 0.2);
  transition: all 300ms ease-in-out;
}
article.image-slider .back-btn:hover,
article.image-slider .next-btn:hover {
  background: rgba(0, 0, 0, 0.5);
}
article.image-slider .next-btn {
  right: 0px;
}
article.image-slider .thumbnails {
  position: absolute;
  bottom: 5px;
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  gap: 5px;
}
article.image-slider .thumbnails img {
  width: 60px;
  cursor: pointer;
  border: 2px solid transparent;
}
article.image-slider .thumbnails img.active {
  border: 2px solid #fff;
}

.video-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.video-slider {
  display: flex;
  justify-content: center;
  place-items: center;
  min-height: 100vh;
  color: #888;
  text-align: center;
  font-family: sans-serif;
  margin-top: 200px;
}
.video-slider li {
  z-index: 1;
  display: inline-block;
  width: 20vw;
  max-width: 250px;
  height: 75vh;
  margin: 0 3rem;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: auto 100%;
  filter: grayscale(0.7) opacity(0.5) contrast(200%);
  transform: skewY(-10deg);
  transition: 0.5s;
}
.video-slider li.highlight {
  filter: grayscale(0) opacity(1);
  transform: scale(1.1);
  box-shadow: 0 25px 50px black;
  z-index: 100;
}
.video-slider li:nth-child(1) {
  background-color: #679966;
  background-image: url("https://i.postimg.cc/wjVFfSmq/avatar01.png");
}
.video-slider li:nth-child(2) {
  background-color: #336799;
  background-image: url("https://i.postimg.cc/432PZVPj/avatar02.png");
}
.video-slider li:nth-child(3) {
  background-color: #FFFF01;
  background-image: url("https://i.postimg.cc/vmZhp3rW/avatar03.png");
}
.video-slider li:nth-child(4) {
  background-color: #990134;
  background-image: url("https://i.postimg.cc/pT9Zdr1r/avatar04.png");
}
.video-slider li:nth-child(1):after,
.video-slider li:nth-child(2):after,
.video-slider li:nth-child(3):after,
.video-slider li:nth-child(4):after {
  content: "NAME";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 40px;
  font-weight: 900;
  color: white;
  text-shadow: 3px 3px #555;
}
.video-slider li:nth-child(1):after {
  content: "AVATAR";
}
.video-slider li:nth-child(2):after {
  content: "AVATAR";
}
.video-slider li:nth-child(3):after {
  content: "AVATAR2";
}
.video-slider li:nth-child(4):after {
  content: "AVATAR2";
}

ul.videos-modal {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}
ul.videos-modal.modal-open {
  z-index: 999;
}
ul.videos-modal .video-box {
  position: relative;
  top: 50%;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  height: 70%;
  transform: translate(-50%, -50%);
}
ul.videos-modal .video-box video {
  width: 100%;
  height: 100%;
  background: #000;
}
ul.videos-modal .video-box button.close-buttons {
  cursor: pointer;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  width: 58px;
  height: 32px;
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.25);
  color: #fff;
  border: none;
  outline: none;
}